@tailwind base;
@tailwind components;
@tailwind utilities;

header ul li {
    @apply px-2;
}
.bg-primary{
    background: #9e62dd
}
.bg-secondary{
    background: #40bcfa
}


.mobile-nav{
    @apply fixed top-10 left-0 bg-gray-900 w-full h-1/4;

}

.mobile-nav li{
    @apply w-full h-full text-center border-b-2;
}
.project-desc{
    @apply absolute left-0 right-0 bottom-0 top-0 bg-primary opacity-0 duration-500 hover:opacity-100;
}
.btn {
    @apply bg-blue-400 px-5 py-2 font-bold hover:border-2 border-white rounded; 
}
html{
    scroll-behavior: smooth;
}





